import auth from 'auth'
import store from 'store'
export const LOGIN_LOGIN = "LOGIN_LOGIN";
export const AJAX_STATE = "AJAX_STATE";
import {combineReducers} from 'redux'
export function isLoginHandler(value){
  return{
    type:AJAX_STATE,
    payload:{
      isLogining:value
    }
  }
}

export function inputChange (value){
  console.log(value)
  return{
    type:LOGIN_LOGIN,
    payload:{
      loginData:value
    },
  }
}

export const loginHandler=()=>{
  return(dispatch,getState)=>{
    dispatch(isLoginHandler(true));
    auth.authorize(getState().loginData.username,
      getState().loginData.password,
      dispatch(isLoginHandler(false)));
  }
};

export const actions = {
  isLoginHandler,
  inputChange,
  loginHandler
};

const ACTION_HANDLERS = {
  [LOGIN_LOGIN]:(state,action)=>Object.assign(state,action.payload),
  [AJAX_STATE]:(state,action)=>Object.assign(state,action.payload)
};
const initialState = {
  loginData:{
    username:'',
    password:'',
  },
  isLogining:false
}
export  function loginInputReducer(state=initialState,action){
  const handler = ACTION_HANDLERS[action.type];
  return handler?handler(state,action):state
}

export default loginInputReducer




